import React, { useState } from 'react'
import AppHeader from '../app-header';
import SearchPanel from '../search-panel';
import TodoList from '../todo-list';
import ItemStatusFilter from '../item-status-filter';
import ItemAddForm from '../item-add-form'
import './app.css'

const App = () => {

  const [state, setState] = useState([
    { label: 'Выучить React', done: false, important: false, id: 1 },
    { label: 'Сделать завтрак', done: false, important: false, id: 2 },
    { label: 'Написать приложение', done: false, important: true, id: 3 }
  ])

  const [search, setSearch] = useState('')
  const [filter, setFilter] = useState('All')
  const [maxId, setMaxId] = useState(1000)

  const onDeleteTodo = id => {
    setState(prev => {
      const newArray = prev.filter(el => el.id !== id)
      return newArray
    })
  }

  const onAddTodo = text => {    
    setMaxId(previd => previd + 1)

    setState(prev => {
      const newArray = [...prev]
      newArray.push({ label: text, important: false, id: maxId })
      return newArray
    })
  }

  const onChangeDone = id => {
    setState(prev => {
      const idx = prev.findIndex(el => el.id === id)
      const newArray = [...prev]
      newArray[idx].done = !prev[idx].done
      return newArray
    })
  }

  const onChangeImportant = id => {
    setState(prev => {
      const idx = prev.findIndex(el => el.id === id)
      const newArray = [...prev]
      newArray[idx].important = !prev[idx].important
      return newArray
    })
  }

  const searchItems = (state, search, filter) => {
    if (filter === 'All') {
      const active = state.filter(el => el.label.toLowerCase().includes(search.toLowerCase()))
      return active
    } else if (filter === 'Active') {
      const active = state.filter(el => !el.done)
      return active.filter(el => el.label.toLowerCase().includes(search))
    } else {
      const active = state.filter(el => el.done)
      return active.filter(el => el.label.toLowerCase().includes(search))
    }
  }

  const onChangeSearch = text => {
    setSearch(text)
  }

  const allFilter = () => {
    setFilter('All')
  }

  const allActive = () => {
    setFilter('Active')
  }

  const allDone = () => {
    setFilter('Done')
  }

  const doneCount = state.filter(el => el.done).length
  const todoCount = state.length - doneCount
  const activeTodos = searchItems(state, search, filter)

  return (
    <div className="todo-app">
      <AppHeader toDo={todoCount} done={doneCount} />
      <div className="top-panel d-flex">
        <SearchPanel onChangeSearch={onChangeSearch} />
        <ItemStatusFilter
          allFilter={allFilter}
          allActive={allActive}
          allDone={allDone}
          filter={filter}
        />
      </div>
      <TodoList
        todos={activeTodos}
        onDeleteTodo={id => onDeleteTodo(id)}
        onChangeDone={id => onChangeDone(id)}
        onChangeImportant={id => onChangeImportant(id)}
      />
      <ItemAddForm onAddTodo={text => onAddTodo(text)} />
    </div>
  )
}

export default App
