import React from 'react';

import './item-status-filter.css';

const ItemStatusFilter = ({ allFilter, allActive, allDone, filter }) => {

  return (
    <div className="btn-group">
      <button
        type="button"
        className={filter === 'All' ? 'btn btn-info' : 'btn btn-outline-secondary'}
        onClick={allFilter}>All</button>
      <button
        type="button"
        className={filter === 'Active' ? 'btn btn-info' : 'btn btn-outline-secondary'}
        onClick={allActive}>Active</button>
      <button
        type="button"
        className={filter === 'Done' ? 'btn btn-info' : 'btn btn-outline-secondary'}
        onClick={allDone}>Done</button>
    </div>
  );
};

export default ItemStatusFilter;
